import React, { Fragment } from 'react'

import courseImg1 from "../../../assests/images/ui-ux.png";
import courseImg2 from "../../../assests/images/Flow.png";

import { Container, Row , Col } from 'reactstrap'
import Header from '../../../components/Header/Header'

const Dr3 = () => {
  return (
    <Fragment>
        <Header/>
    <Container>
        <Row>
          <Col lg="12" className="mb-5 mt-4">
            <div className="course__top d-flex justify-content-between align-items-center">
              <div className="course__top__left w-50">
                <h2 className='mb-3'> </h2>
                <span>  Flow Chart</span>
                    <img src={courseImg1} className='mt-3 mb-3 img-fluid img-thumbnail' alt="" />
                <p style={{fontWeight:'bold', color: 'green'}}>           :</p>
                <p>1-    "(Flow Chart)"</p>
                <section style={{marginTop:"-40px"}}>
                    <p style={{color:'green', fontWeight: 'bold'}}> ":   Flow Chart</p>
                    <div className='d-flex justify-content-end'>
                    <p>               .</p>
                    </div>
                    <img src={courseImg2} className='mt-3 mb-3 img-fluid' alt="" />
                    <section>
                    <p style={{fontWeight: 'bold', color: 'green', marginTop: '-20px'}}>   :</p>
                    <p>1-         .</p>
                    <p>2-      .</p>
                    <p>3-         </p>
                    </section>
                    <p style={{marginTop: '-15px'}} className='mb-5'>                                          </p>
                    <p>                            </p>
                    <div className='d-flex justify-content-center'>
                    <a href="/Dr4" className='btn btn-success mt-2'>   VB.Net</a>
                    </div>
                </section>
              </div>
            </div>
          </Col>
        </Row>
    </Container>
    </Fragment>
  )
}

export default Dr3