import React, { Fragment } from 'react'

import courseImg1 from "../../../assests/images/web-design.png";

import { Container, Row , Col } from 'reactstrap'
import Header from '../../../components/Header/Header'

const Dr1 = () => {
  return (
    <Fragment>
        <Header/>
    <Container>
        <Row>
          <Col lg="12" className="mb-5 mt-4">
            <div className="course__top d-flex justify-content-between align-items-center">
              <div className="course__top__left w-50">
                <h2 className='mb-3'> </h2>
                <span>  Problem Solving</span>
                    <img src={courseImg1} className='mt-3 mb-3 img-fluid img-thumbnail' alt="" />
                <p style={{fontWeight:'bold', color: 'green'}}>           :</p>
                <p>1-   "(prblem Solving)"</p>
                <p>2-     "(prblem Solving Stages)"</p>
                <p>3-     "( Algorithm)"</p>
                <section style={{marginTop:"-40px"}}>
                    <p>                                    </p>
                    <p style={{color:'green', fontWeight: 'bold'}}> ":  </p>
                    <p>                </p>
                    <p>           <a href="/Dr2" style={{color: 'green', fontSize:'12px'}}> </a>     </p>
                    <div className='d-flex justify-content-center'>
                    <a href="/Dr2" className='btn btn-success'>  </a>
                    </div>
                </section>
              </div>
            </div>
          </Col>
        </Row>
    </Container>
    </Fragment>
  )
}

export default Dr1