import React, { Fragment } from 'react'

import courseImg1 from "../../../assests/images/graphics-design.png";
import courseImg2 from "../../../assests/images/Asv.png";

import { Container, Row , Col } from 'reactstrap'
import Header from '../../../components/Header/Header'

const Dr2 = () => {
  return (
    <Fragment>
        <Header/>
    <Container>
        <Row>
          <Col lg="12" className="mb-5 mt-4">
            <div className="course__top d-flex justify-content-between align-items-center">
              <div className="course__top__left w-50">
                <h2 className='mb-3'> </h2>
                <span> Algorithms</span>
                    <img src={courseImg1} className='mt-3 mb-3 img-fluid img-thumbnail' alt="" />
                <p style={{fontWeight:'bold', color: 'green'}}>           :</p>
                <p>1-     "( Algorithm)"</p>
                <section style={{marginTop:"-40px"}}>
                    <p style={{color:'green', fontWeight: 'bold'}}> ":   Problem Solving</p>
                    <div className='d-flex justify-content-end'>
                    <p>            .</p>
                    <img src={courseImg2} className='mt-3 mb-3 w-50 img-fluid' alt="" />
                    </div>
                    <p style={{color: 'green', fontWeight: 'bold'}}> ":    Problem Solving Stages</p>
                    <p>      :</p>
                    <p style={{fontWeight: 'bold'}}>1-!@   problem Definition</p>
                    <p>          .</p>
                    <p style={{fontWeight: 'bold'}}>2-!@    Algorithm</p>
                    <p>             .</p>
                    <p>                         ( Algorithm )                     ( Flow Chart )</p>
                    <div className='d-flex justify-content-center'>
                    <a href="/Dr3" className='btn btn-success'> </a>
                    </div>
                </section>
              </div>
            </div>
          </Col>
        </Row>
    </Container>
    </Fragment>
  )
}

export default Dr2